import axios from './index'

const loginUser = (data) => {
    return axios.request({
        url: '/login',
        method: 'post',
        data
    })
}

const testUrl = (data) => {
    return axios.request({
        url: '/user/test',
        method: 'post',
        data
    })
}

export {
    loginUser,
    testUrl
}